import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Room } from 'colyseus.js';
import {Client, RoomAvailable } from 'colyseus.js';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class LobbyServiceService {

  private _URI = 'https://cff-live.herokuapp.com/';//'localhost:25670';
  private client : Client;
  private _lobby: any ;
  allRooms$ = new BehaviorSubject<any>([]);
  allRooms : Room[] = [];

  constructor( ) {
    this.client = new Client('ws://' + this._URI);
    this.getRooms();
  }

  createRoom(playerName: string){
    return this.client.joinOrCreate('races', { name: playerName}).then(
      (room) => {
        this.allRooms.push(room);
        console.log(room);
        return room;
      }
    ).catch(
      (err) => console.log(err)
    );
  }

  joinRoom(roomId: string, playerName: string):  Promise<Room<any>>  {
    return this.client.getAvailableRooms().then(
      rooms => {
        this.allRooms = rooms as any
        this.allRooms$.next(this.allRooms);
        return this.client.joinById(roomId, { name: playerName });
      }
    );
  }

  getRoom(roomId:string): Promise<Room<any>>{
    return this.client.getAvailableRooms().then(
      rooms => {
        this.allRooms = rooms as any
        this.allRooms$.next(this.allRooms);
        return this.allRooms.find(el => el.id == roomId);
      }
    );
  }

  getRooms(){
    this.client.getAvailableRooms().then(
      rooms => {
        this.allRooms = rooms as any
        this.allRooms$.next(this.allRooms);
      }
    );
  }
}
