import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Room } from 'colyseus';
import { LobbyServiceService } from 'src/app/services/lobby-service.service';
import { RoomService } from 'src/app/services/room.service';

@Component({
  selector: 'app-lobby',
  templateUrl: './lobby.component.html',
  styleUrls: ['./lobby.component.sass']
})
export class LobbyComponent implements OnInit {

  playerName:string='';
  rooms = [];
  stage;

  constructor(private lobbyService:LobbyServiceService, private roomService: RoomService, private router : Router) { }

  ngOnInit(): void {
    this.lobbyService.allRooms$.subscribe(el => {
      this.rooms = el;
      console.log(this.rooms);
    });
  }

  createRoom(){
    let ret = this.lobbyService.createRoom(this.playerName);
    ret.then(
      (room) => {
        this.roomService.room = room as any;
        console.log(room);
        this.router.navigate(['/rooms', (room as any).id  ]);
      }
    ).catch(
      err => console.error(err)
    )
    //console.log(ret);
  }

  joinRoom(room:Room){
    this.lobbyService.joinRoom(room.roomId, this.playerName).
    then(
      (room) => {
        this.roomService.room = room as any;
        this.router.navigate(['/rooms', room.id  ]);
      }
    ).
    catch(
      err => console.error(err)
    )

  }

}
