import { Component, OnInit } from '@angular/core';

import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Room } from 'colyseus.js';
import { LobbyServiceService } from 'src/app/services/lobby-service.service';
import { RoomService } from 'src/app/services/room.service';

import { GameState } from '../../../../../../state/GameState';
import { Rider } from '../../../../../../backend/Player';
import { Subject, Subscription } from 'rxjs';
import { debounceTime, map } from 'rxjs/operators';

@Component({
  selector: 'app-room',
  templateUrl: './room.component.html',
  styleUrls: ['./room.component.sass']
})
export class RoomComponent implements OnInit {

  room: Room;
  state: GameState;
  formattedClockTime:string;
  currentEffort:number = 60;
  playerName: string;
  rider: Rider;

  private modelChanged: Subject<number> = new Subject<number>();
  private subscription: Subscription;
  debounceTime = 500;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private lobbyService:LobbyServiceService,
    private roomService:RoomService,
  ) { }

  ngOnInit() {
    const roomId = this.route.snapshot.paramMap.get('id');
    if(this.roomService.room){
      this.room = this.roomService.room as any;
      this.subscribeToStateChanges();
    }else{
      this.lobbyService.joinRoom(roomId, this.playerName)
      .then(
        (room) => {
          this.roomService.room = room as any;
          this.room = room;
          this.subscribeToStateChanges();
        })
      .catch(
          err => this.router.navigate(['/rooms'])
        )
    }

    this.subscription = this.modelChanged
    .pipe(
      debounceTime(this.debounceTime),
    )
    .subscribe(() => {
      this.sendEffort();
    });

  }

  ready(){
    this.room.send("ready", true);
  }

  formatTime(tot_seconds:number){
    const minutes = Math.floor(tot_seconds / 60);
    const seconds = ((tot_seconds % 60));
    return minutes + ":" + (seconds < 10 ? '0' : '') + seconds.toFixed(0);
  }

  subscribeToStateChanges(){
    (this.roomService.room as any).onStateChange((newState: GameState) => {
      //Clear or do stuff here
      this.state = newState;
      this.formattedClockTime = this.formatTime(this.state.turn_time);
      console.log('State: ' + newState);
    });

    (this.roomService.room as any).onMessage("initializeRider", (rider) => {
      console.log("message received from server");
      console.log(rider);
      this.rider = rider;
    });
  }

  effortChange( event){
    this.currentEffort = event;
    this.modelChanged.next(this.currentEffort);

  }

  sendEffort(){
    (this.roomService.room as any).send("strategy", this.currentEffort);
  }


  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }
}
